@import url('https://fonts.googleapis.com/css2?family=Noto+Sans:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Handjet:wght@200&display=swap');

body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif, 'Noto Sans JP';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0 auto;
  background-color:rgb(113, 202, 202);
}

.Generate{

}

.header{
  color: aliceblue;
  text-align: center;
  margin: 0;
  padding: 10px;
  font-size: 30px;
}

.formLabel{
  display: flex;
  width: 300px;
  margin: 0 auto;
  color: white;
  font-size: 14px;
}

.formItem{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0px auto 50px auto;
}


.formItem input{
  border: none;
  height: 30px;
  padding: 10px;
  width: 300px;
  font-size: 20px;

  border-radius: 5px;
}

.formItem input:focus{
  outline: none;
  background-color: azure;
  opacity: 0.5;
}

.formItem button{
  width: 200px;
  margin: 60px auto;
  font-size: 20px;
  padding: 20px;
  background-color:rgb(234, 189, 196);
  border-radius: 20px;
  cursor: pointer;
  color: white;
  border: none;
}

.loading{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.footer{
  position: fixed;
  bottom: 0;
  height: 40px;
  background-color: white;
  width: 100%;
}

.playlist{
  width: 85%;
  margin: 0 auto;
}

.playlist h2{
  color: white;
}

.playlist1, .playlist2 {
  display: flex;
  gap: 20px;

  overflow: scroll;
}

.playlist img{
  width: 200px;
}

.modal{
  position: absolute;
  top: 50%;
  left: 50%;
  right: auto;
  bottom: auto;
  transform: translate(-50%, -50%);
  width:400px;
  height: 400px;
  border: none;
  background-color: white;
  text-align: center;
  border-radius: 5px;
  outline: none;
}

.modal img{
  width: 300px;
  margin-top:10px;
}

.rhap_container{
  position: absolute;
  bottom:0;
}
